 function Request(options){
	// const baseUrl = '1.85.30.230:8081';
	let timer = null //  800msloading,
	const api = 'https://xcx.sxstyy.com:8081/index.php' 
	const promise = new Promise(function(resolve,reject){
		if(!timer) {
			timer = setTimeout(() => {
				uni.showLoading({
					title:'',
					mask:true
				})
				// console.log('');
				// console.log(timer);
				}, 1000);
		}
		// uni.showLoading({
		// 	title:'',
		// 	mask:true
		// })
		uni.request({
			url:api,//+options.url
			method:options.method ||"GET",
			data:{proc:'pro_Get_dataInfo',...options.data},
			dataType:options.dataType || "json",
			header:options.header || {},
			success:function(res){
				// console.log(timer);
				if(timer){
					clearTimeout(timer);
					// console.log(''+ timer);
					uni.hideLoading()
				}
				
				// console.log(''+ res);
				if(res.statusCode == 200){
					resolve(res.data)
				}else{
					resolve(res.data)
				}
			},
			fail:function(res){
				
				if(timer){
					clearTimeout(timer);
					// console.log('');
				}
				
				uni.hideLoading()
				// console.log(''+ res);
				reject('')
			}
		})
	})
	return promise;
}
export default Request
